@import "../../vars-mixins.scss";

.about-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    header{
        width: 95%;
        margin-top: 2rem;
        padding: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid rgb(182, 243, 212);
        border-radius: 5rem;
        box-shadow: 12px 17px 51px rgba(32, 150, 95, 0.22);
        // backdrop-filter: blur(6px);
        transition: all 0.5s;
        user-select: none;
        &:hover {
            border: 1px solid rgb(172, 248, 210);
            transform: scale(1.02);
        }
          
        &:active {
            transform: scale(0.95) rotateZ(1.7deg);
        }
        
        img{
            width: 200px;
            align-self: center;
            margin:1rem;
            margin-top: 3rem;
        }
        article{
            margin:1rem;
            
            h1{
                font-size: 3rem;
                
                margin-top: 2rem;
            }
            p{
                font-size: 1.5rem;
            }
        }
    }
    .tech-container{
        width: 95%;
        margin-top: 2rem;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 1px solid rgb(182, 243, 212);
        border-radius: 5rem;
        box-shadow: 12px 17px 51px rgba(32, 150, 95, 0.22);
        // backdrop-filter: blur(6px);
        transition: all 0.5s;
        user-select: none;
        &:hover {
            border: 1px solid rgb(172, 248, 210);
            transform: scale(1.02);
        }
          
        &:active {
            transform: scale(0.95) rotateZ(1.7deg);
        }
        .tech-list{
            display: flex;
            flex-wrap: wrap;
            gap: 2%;
            justify-content: center;
            .one-tech{
                width: 32%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                gap: 5%;
                margin-bottom: 2rem;
                img{
                    width: 50px;
                }
                label{
                    font-size: 2rem;
                }
            }
        }
    }
}