
nav{
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
    width: 1200px;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
    
}
.nav-buttons{
    display: flex;
    gap: 1rem;
    width: 60%;
    justify-content: center;
    align-items: center;
}

a{
    scroll-behavior: smooth;
}











button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    font-family: inherit;
    font-size: 18px;
    
   }
   
   button{
    font-weight: 600;
    color: #382b22;
    text-transform: uppercase;
    padding: 1.25em 2em;
    background: #99f1e1;
    border: 2px solid #629f94;
    border-radius: 0.75em;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transition: background 150ms cubic-bezier(0, 0, 0.58, 1), -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), background 150ms cubic-bezier(0, 0, 0.58, 1), -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
   }
   
   button::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #49716a;
    border-radius: inherit;
    -webkit-box-shadow: 0 0 0 2px #99f1e1, 0 0.625em 0 0 #83b0a8;
    box-shadow: 0 0 0 2px #99f1e1, 0 0.625em 0 0 #426961;
    -webkit-transform: translate3d(0, 0.75em, -1em);
    transform: translate3d(0, 0.75em, -1em);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), box-shadow 150ms cubic-bezier(0, 0, 0.58, 1), -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1), -webkit-box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
   }
   
   button:hover {
    background: #5fe2ca;
    -webkit-transform: translate(0, 0.25em);
    transform: translate(0, 0.25em);
   }
   
   button:hover::before {
    -webkit-box-shadow: 0 0 0 2px #99f1e1, 0 0.5em 0 0 #99f1e1;
    box-shadow: 0 0 0 2px #99f1e1, 0 0.5em 0 0 #426961;
    -webkit-transform: translate3d(0, 0.5em, -1em);
    transform: translate3d(0, 0.5em, -1em);
   }
   
   button:active {
    background: #99f1e1;
    -webkit-transform: translate(0em, 0.75em);
    transform: translate(0em, 0.75em);
   }
   
   button:active::before {
    -webkit-box-shadow: 0 0 0 2px #99f1e1, 0 0 #99f1e1;
    box-shadow: 0 0 0 2px #99f1e1, 0 0 #426961;
    -webkit-transform: translate3d(0, 0, -1em);
    transform: translate3d(0, 0, -1em);
   }

